

/*---------------Inheritances-------------*/

$background-color: #fafafaff;
$footer: #ecececff;
$text-color: #484848ff;
$highlight: #000000ff;
$gold: #dbbb53ff;


%center {
    display: flex;
    align-items: center;
    justify-content: center;
  }

%shadow {
    -webkit-box-shadow: 0px 3px 6px 0 rgba(0, 0, 0, 0.16);
    -moz-box-shadow: 0px 3px 6px 0 rgba(0, 0, 0, 0.16);
    box-shadow: 0px 3px 6px 0 rgba(0, 0, 0, 0.16);
  }
  
/////////////////////////////////////////////

*{
    padding: 0;
    margin: 0;
}

body {
    overflow-x: hidden;
    background-color: $background-color;
  }

html{
    font-family: sweet-sans-pro, sans-serif;
}


/* LOADER / SPINNER */
/* ---------- loader styling ---------- */
#loader {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999999999;
    overflow: hidden;
    background-color: none;
    text-align: center;
  }
  
  .spinner {
    border: 5px solid $text-color;
    border-radius: 50%;
    border-top: 5px solid $text-color;
    width: 50px;
    height: 50px;
    -webkit-animation: spin 1s linear infinite;
    /* Safari */
    animation: spin 1s linear infinite;
    display: inline-block;
    vertical-align: middle;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }


/* ---------- HIDE ---------- */
.hide {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 0.4s, opacity 0.4s linear;
  }

/* smykkegalleri */

.overview-grid-container{
    display: grid;
    grid-template-columns: repeat(6, 115px);
    grid-template-rows: 115px;
    grid-gap: 13px;

img{
    max-width: 115px;
    max-height: 115px;
    cursor: pointer;
}
}


.clear1{
    grid-column: 6;
    grid-row: 1;
}
.clear2{
    grid-column: 1;
    grid-row: 2;
}
.clear3{
    grid-column: 5 / 7;
    grid-row: 3;
}
.clear4{
    grid-column: 1;
    grid-row: 4;
}
.clear4end{
    grid-column: 6;
    grid-row: 4;
}

/* Ydre grid */
